
import { mapGetters, mapActions, mapMutations } from 'vuex';
import { api } from '@/utils/api.js';
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data() {
return {
nvabarData: {
showCapsule: 1,
title: '充值',
},

isSelect: false,
methodList: [],
methodType: {
"pay_code": 2,
"pay_name": "微信",
"icon": 'http://img.xiepinhui.com.cn/mobile/bank/wxpay.png'
},
choose_list:[],
activeTab:'',
recharge_phone:'',
}
},
created() {
this.getList();
this.rechargeWayList();
},
mounted() {

},
methods: {
async getList(){
let data = {
plat: 3,
account: this.account,
token: this.token
}
const [err, res] = await api.rechargeMoneyTab(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
this.choose_list = res.data.choose_list;
this.choose_id = res.data.choose_list[0].choose_id;
}
},
getCurrent(tabIndex, chooseId){
this.activeTab = tabIndex;
this.choose_id = chooseId;
},
showBankList(){
this.isSelect = !this.isSelect;
},
async rechargeWayList(){
let data = {
token: this.token,
account: this.account,
scene_id: 3 
}
const [err, res] = await api.rechargeWayList(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
this.methodList = res.data.list;
}
},
selectType(index){
this.methodType = this.methodList[index];
this.showBankList();
},
linkToBankCard(){
this.$router.push('/addCard')
},
async goToRecharge(){
var that = this;
var account_mobile = that.recharge_phone;
var phoneReg = /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/;
if (!phoneReg.test(account_mobile)) {
return this.$vux.toast.text('请输入有效的手机号！'); 
}
var choose_id = this.choose_id;
let data = {
account: this.account,
token: this.token,
openid: this.user.openid,
account_mobile,
choose_id,
pay_code: 2,  //1支付宝，2微信
}
const [err, res] = await api.offline_dopay(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
console.log(res)
}
}
},
computed: {
...mapGetters(['user', 'account', 'token'])
}
}



